___CSS_LOADER_EXPORT___.push([module.id, `/* General Card Styling */
.card-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff; /* White background for the card */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    width: 300px; /* Fixed width */
    padding: 16px;
    margin: 16px auto; /* Center horizontally */
    font-family: Arial, sans-serif;
    color: #333; /* Dark text for readability */
}

/* Card Top Section */
.card-top.current {
    display: flex;
    justify-content: space-between; /* Space between day and time */
    width: 100%;
    margin-bottom: 16px; /* Add space below */
}

.day.current {
    font-size: 18px;
    font-weight: bold;
    color: #555; /* Muted dark color */
}

.time.current {
    font-size: 14px;
    color: #777; /* Subtle time color */
}

/* Card Middle Section */
.card-middle.current {
    display: grid;
    grid-template-rows: auto auto auto;
    grid-gap: 15px; /* Space between rows */
    text-align: center;
    margin: 1rem 0;
    width: 100%;
}

.icon.current img {
    width: 80px; /* Size the weather icon */
    height: 80px;
    margin-bottom: 8px;
}

.temp.current {
    font-size: 24px;
    font-weight: bold;
    color: #0f0201; /* Emphasize temperature in red */
}

.condition.current {
    font-size: 16px;
    font-weight: 500;
    color: #555; /* Muted dark color */
}

.description.current {
    font-size: 14px;
    color: #777; /* Subtle text for description */
    margin-top: 4px;
}

/* Card Bottom Section */
.card-bottom.current {
    display: flex;
    gap: 8px; /* Add space between items */
    align-items: center;
    justify-content: space-around;
    width: 100%;
    margin-top: 10px;
}

.feelslike.current,
.humidity.current {
    display: inline;
    font-size: 14px;
    color: #555; /* Muted text for labels */
}

.card-bottom.current .feelslike-value.current,
.card-bottom.current .humidity-value.current {
    font-weight: bold;
    color: #333; /* Slightly darker for emphasis */
}

/* Responsive Design */
@media (max-width: 400px) {
    .card-content {
        width: 90%; /* Full width on smaller screens */
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/currentCard.css"],"names":[],"mappings":"AAAA,yBAAyB;AACzB;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,yBAAyB,EAAE,kCAAkC;IAC7D,mBAAmB,EAAE,oBAAoB;IACzC,wCAAwC,EAAE,4BAA4B;IACtE,YAAY,EAAE,gBAAgB;IAC9B,aAAa;IACb,iBAAiB,EAAE,wBAAwB;IAC3C,8BAA8B;IAC9B,WAAW,EAAE,8BAA8B;AAC/C;;AAEA,qBAAqB;AACrB;IACI,aAAa;IACb,8BAA8B,EAAE,+BAA+B;IAC/D,WAAW;IACX,mBAAmB,EAAE,oBAAoB;AAC7C;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,WAAW,EAAE,qBAAqB;AACtC;;AAEA;IACI,eAAe;IACf,WAAW,EAAE,sBAAsB;AACvC;;AAEA,wBAAwB;AACxB;IACI,aAAa;IACb,kCAAkC;IAClC,cAAc,EAAE,uBAAuB;IACvC,kBAAkB;IAClB,cAAc;IACd,WAAW;AACf;;AAEA;IACI,WAAW,EAAE,0BAA0B;IACvC,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,cAAc,EAAE,iCAAiC;AACrD;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,WAAW,EAAE,qBAAqB;AACtC;;AAEA;IACI,eAAe;IACf,WAAW,EAAE,gCAAgC;IAC7C,eAAe;AACnB;;AAEA,wBAAwB;AACxB;IACI,aAAa;IACb,QAAQ,EAAE,4BAA4B;IACtC,mBAAmB;IACnB,6BAA6B;IAC7B,WAAW;IACX,gBAAgB;AACpB;;AAEA;;IAEI,eAAe;IACf,eAAe;IACf,WAAW,EAAE,0BAA0B;AAC3C;;AAEA;;IAEI,iBAAiB;IACjB,WAAW,EAAE,iCAAiC;AAClD;;AAEA,sBAAsB;AACtB;IACI;QACI,UAAU,EAAE,kCAAkC;IAClD;AACJ","sourcesContent":["/* General Card Styling */\n.card-content {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    background-color: #ffffff; /* White background for the card */\n    border-radius: 10px; /* Rounded corners */\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */\n    width: 300px; /* Fixed width */\n    padding: 16px;\n    margin: 16px auto; /* Center horizontally */\n    font-family: Arial, sans-serif;\n    color: #333; /* Dark text for readability */\n}\n\n/* Card Top Section */\n.card-top.current {\n    display: flex;\n    justify-content: space-between; /* Space between day and time */\n    width: 100%;\n    margin-bottom: 16px; /* Add space below */\n}\n\n.day.current {\n    font-size: 18px;\n    font-weight: bold;\n    color: #555; /* Muted dark color */\n}\n\n.time.current {\n    font-size: 14px;\n 