___CSS_LOADER_EXPORT___.push([module.id, `.card-content.hourly {
    border: 1px solid #d3d3d3;
    border-radius: 8px;
    padding: 16px;
    min-width: 250px;
    max-width: 350px;
    height: 65%;
    background-color: #f9f9f9;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
}

.card-top.hourly {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 12px;
}

.card-top.hourly p {
    margin: 0;
    font-size: 18px;
    color: #333;
}

.card-top.hourly .hour {
    color: #333;
    font-weight: 600;
}

.card-icon.hourly {
    margin-top: 3rem;
    min-width: 100px;
    min-height: 100px;
    max-height: 100px;
    max-width: 100px;
    justify-self: center;
}

.card-middle.hourly {
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 1rem;
}

.temp-value.hourly {
    width: 100%;
    display: flex;
    justify-content: space-around;
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
}

.weather-condition.hourly {
    font-size: 1rem;
    color: #333;
    align-self: center;
}

.card-bottom.hourly {
    font-size: 14px;
    color: #333;
    align-self: center;
}

.humidity.hourly {
    margin-top: 5px;
}

.humidity-value.hourly {
    font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/styles/hourlyCard.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;IACb,gBAAgB;IAChB,gBAAgB;IAChB,WAAW;IACX,yBAAyB;IACzB,0CAA0C;IAC1C,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,SAAS;IACT,eAAe;IACf,WAAW;AACf;;AAEA;IACI,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,sBAAsB;IACtB,QAAQ;IACR,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,6BAA6B;IAC7B,iBAAiB;IACjB,iBAAiB;IACjB,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":[".card-content.hourly {\n    border: 1px solid #d3d3d3;\n    border-radius: 8px;\n    padding: 16px;\n    min-width: 250px;\n    max-width: 350px;\n    height: 65%;\n    background-color: #f9f9f9;\n    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);\n    font-family: Arial, sans-serif;\n}\n\n.card-top.hourly {\n    display: flex;\n    justify-content: space-between;\n    width: 100%;\n    margin-bottom: 12px;\n}\n\n.card-top.hourly p {\n    margin: 0;\n    font-size: 18px;\n    color: #333;\n}\n\n.card-top.hourly .hour {\n    color: #333;\n    font-weight: 600;\n}\n\n.card-icon.hourly {\n    margin-top: 3rem;\n    min-width: 100px;\n    min-height: 100px;\n    max-height: 100px;\n    max-width: 100px;\n    justify-self: center;\n}\n\n.card-middle.hourly {\n    display: flex;\n    width: 100%;\n    flex-direction: column;\n    gap: 8px;\n    margin-bottom: 1rem;\n}\n\n.temp-value.hourly {\n    width: 100%;\n    display: flex;\n    justify-content: space-around;\n    font-size: 1.5rem;\n    font-weight: bold;\n    color: #333;\n    margin-bottom: 5px;\n}\n\n.weather-condition.hourly {\n    font-size: 1rem;\n    color: #333;\n    align-self: center;\n}\n\n.card-bottom.hourly {\n    font-size: 14px;\n    color: #333;\n    align-self: cent